@font-face {
    font-family: cyberpunk;
    src: url(asset/font/cyberpunk.ttf);
    font-weight: 800;
}

@font-face {
    font-family: caveat;
    src: url(asset/font/Caveat-VariableFont_wght.ttf);
    font-weight: 800;
}

body {
    text-align: center;
    padding: 2.5%;
    font-family: 'Cyberpunk';
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
}



input {
    color: rgb(255, 255, 255);
    font-family: 'caveat';
    font-style: 'italic';
    background-color: #021114;
    border:none !important;
    border-bottom: 1px solid #26dafd !important;
}

input:focus {
    outline: none !important;
    /* border:1px solid red; */
    /* box-shadow: 0 0 10px #719ECE; */
}


input::placeholder {
    color: grey !important;
    /* font-size: 1.2em; */
    font-style: italic !important;
    color: '#26dafd' !important;
} 

/* input.placeholderred::-webkit-input-placeholder {
    color: '#26dafd' !important;
} */

h1, h2, h3, h4, h5 {
    font-family: 'cyberpunk';
}